//*color mixed
$color: #1F2041;
$purple: #BC9CFF;
$green: #6fcf97;
$dark-100:$color;
$dark-75: rgba(31, 32, 65,0.75);
$dark-50: rgba(31, 32, 65,0.50);
$dark-25: rgba(31, 32, 65,0.25);
$dark-5: rgba(31, 32, 65,0.05);
$btn-bg: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%); // цвет фона кнопок
$max-width:1431px;  // Максимальная ширина страницы
$min-width:320px;   // Минимальная ширина страницы
$height-header:70px; //высота шапки с меню
$width-content: ($max-width - 280px)/$max-width*100%; // ширина блока с контентом с учётом отступов по 140px по краям
$br: 1px solid #666; // граница для тестов

// Подключение основных scss блоков
@import "../src/fonts/font";
@import "../src/header/header";
@import "../src/footer/footer";


body{
    margin: 0 auto;
    padding:0px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size:14px;
    background-color: #e5e5e5;

}
.wrapper{
    max-width:$max-width;
    min-width:$min-width;
    padding:0px;
    margin:0;
    background-color: #e5e5e5;
}
